<template>
  <div id="detail">
    <van-overlay
      :show="load"
      :z-index="10000"
      class="loadBox"
      :lock-scroll="true"
    >
      <van-loading
        class="load"
        type="spinner"
        color="#1989fa"
        :vertical="true"
        v-if="load"
      />
    </van-overlay>
    <div class="content">
      <div class="swiperBox">
        <van-swipe class="my-swipe" :autoplay="3000">
          <van-swipe-item v-for="(image, index) in data.acImages" :key="index">
            <img :src="image" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="tip">
        《亮剑》导演张前执导 CCTV-1黄金档同步热播 新时光全网独家
      </div>
      <div class="videoBaseInfo">
        <div class="videoTitle">
          <div class="posterBox">
            <img :src="data.poster_v" alt="" />
          </div>
          <div class="rightBox">
            <p class="videoName">{{ data.name }}</p>
            <p>
              <span class="episodes">共{{ data.video_num }}集</span>
              <span v-for="(item, index) in data.tags" :key="index">
                <span v-if="index !== 0">/</span>
                {{ item }}
              </span>
            </p>
            <p>导演 ：{{ director.name }}</p>
            <p>
              领衔主演 ：<span v-for="(item, index) in actors" :key="index"
                >{{ item.actor.name }}、</span
              >
            </p>
          </div>
        </div>
        <div class="videoBrief">
          <div
            class="BriefBox"
            :class="{ shrink: showToggleBtn && showVideoBrief }"
          >
            <div
              class="videoBriefText"
              ref="videoBriefText"
              v-html="introduction"
            ></div>
            <div v-if="showToggleBtn && showVideoBrief" class="ellipsis">
              ...
            </div>
          </div>
          <p class="changeBrief" v-if="showToggleBtn" @click="changeBrief">
            {{ showVideoBrief ? "展开" : "收起" }}
          </p>
        </div>
        <div class="videoDetail" v-html="data.intro"></div>
        <!--  -->
        <div class="videoBuy">
          <div class="price">
            <span class="realPrice">￥{{ data.real_price }}</span>
            <span class="deletePrice">￥{{ data.price }}</span>
            <span class="rightMarker">{{ data.video_num }}集</span>
          </div>
          <div class="tryWatch">
            <p @click="toWatch">免费试看</p>
          </div>
          <div class="buyNow" v-if="!data.is_buy" @click="WXPay">立即抢购</div>
          <div class="buyNow" v-else>已购买</div>
        </div>
        <div class="shareBtn" @click="toShare">
          <p>邀请好友得</p>
          <p>
            <span>{{ data.distribute_money }}</span>
            元
          </p>
        </div>
      </div>
    </div>
    <to-home :toWhere="'personalCenter'"></to-home>
    <invite-login
      v-if="showInviter"
      :showInviter="showInviter"
      :from_user_id="from_user_id"
      :ac_id="ac_id"
      @closeDialog="closeDialog"
    >
    </invite-login>
  </div>
</template>

<script>
import {
  getDetailData,
  getWXPay,
  openidLogin,
} from "../assets/javascript/apis.js";
import toHome from "../components/toHome.vue";
import inviteLogin from "../components/inviteLogin.vue";

export default {
  components: {
    toHome,
    inviteLogin,
  },
  data() {
    return {
      introduction: "",
      showToggleBtn: false,
      showVideoBrief: true,
      load: false,
      getDataFlag: true,
      data: {},
      is_buy: false,
      showInviter: false,
      ac_id: 0,
      from_user_id: 0,
      director: {},
      actors: [],
    };
  },
  created() {
    //如果没有获得到对应的数据，则跳转向错误页面
    if (
      this.$store.state.distributionInfo.ac_id &&
      this.$store.state.distributionInfo.from_user_id
    ) {
      this.getData();
      //获取到分销参数，弹出分销弹框
      this.ac_id = this.$store.state.distributionInfo.ac_id;
      this.from_user_id = this.$store.state.distributionInfo.from_user_id;
      this.showInviter = true;
    } else {
      this.$router.push("/errorPage");
    }

    //设置分享链接
    this.$shareConfig();
  },
  methods: {
    closeDialog() {
      this.showInviter = false;
    },
    //获取页面数据
    getData() {
      this.load = true;
      if (!this.getDataFlag) return;
      this.getDataFlag = false;
      const data = {
        ac_id: this.$store.state.distributionInfo.ac_id
          ? this.$store.state.distributionInfo.ac_id
          : "",
        user_id: this.$store.state.appUserInfo.id
          ? this.$store.state.appUserInfo.id
          : "",
      };
      getDetailData(data).then((res) => {
        console.log(res);
        this.data = res;
        this.actors = res.actors.filter(function (item, index, arr) {
          return item.actor_name !== "导演";
        });
        this.director = res.actors.find(function (item, index, arr) {
          return item.actor_name == "导演";
        }).actor;
        this.introduction = res.introduction;
        this.is_buy = res.is_buy;
        this.load = false;
        this.getDataFlag = true;
      });
    },
    //购买视频
    WXPay() {
      if (this.$store.state.WXUserInfo.open_id) {
        openidLogin({
          openid: this.$store.state.WXUserInfo.open_id,
        }).then((res) => {
          console.log(res);
          this.$store.commit("setAppUserInfo", {
            token: res.token,
            id: res.data.id,
          });

          if (res.status == "none") {
            this.$router.push("login");
          } else {
            //验证是否绑定了账号没有去登陆
            const data = {
              ac_id: this.$store.state.distributionInfo.ac_id,
              from_user_id: this.$store.state.distributionInfo.from_user_id,
              user_id: this.$store.state.appUserInfo.id,
              openid: this.$store.state.WXUserInfo.open_id,
            };

            getWXPay(data).then((res) => {
              console.log(res);
              //发起支付请求
              this.$payConfig(
                res.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                res.nonceStr, // 支付签名随机串，不长于 32 位
                res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                res.paySign, // 支付签名
                function (res) {
                  window.location.href =
                    "https://h5.newtiming.com/payResult?result=true";
                },
                function (res) {
                  window.location.href =
                    "https://h5.newtiming.com/payResult?result=false";
                }
              );
            });
          }
        });
      } else {
        let pureUrl = encodeURIComponent(
          window.location.origin + this.$route.path
        );
        location.replace(
          "https://app.newtiming.com/api/v1/socials/authorizations?redirect_url=" +
            pureUrl
        );
      }
    },
    //去往个人中心页面
    toUser() {
      this.$router.push({ name: "personalCenter" });
    },
    //去往分享页面
    toShare() {
      this.$router.push({ name: "share" });
    },
    //改变视频简介信息状态
    changeBrief() {
      this.showVideoBrief = !this.showVideoBrief;
    },
    //免费试看
    toWatch() {
      window.location.href = "https://www.newtiming.com/h5/detailPlay";
    },
  },
  watch: {
    introduction: function () {
      this.$nextTick(function () {
        let videoBriefHeight = parseFloat(
          window.getComputedStyle(this.$refs.videoBriefText).height
        );
        let briefLineHeight = parseFloat(
          window.getComputedStyle(this.$refs.videoBriefText).lineHeight
        );
        console.log(videoBriefHeight, briefLineHeight);
        if (videoBriefHeight > briefLineHeight * 3) {
          console.log(1);
          this.showToggleBtn = true;
        } else {
          console.log(2);
          this.showToggleBtn = false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#detail {
  height: 100%;
  width: 100%;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .swiperBox {
      flex-shrink: 0;
      width: 100%;
      height: 221px;
      /deep/.my-swipe {
        width: 100%;
        height: 221px;

        .van-swipe__track {
          .van-swipe-item {
            img {
              width: 100%;
              height: 221px;
            }
          }
        }
        .van-swipe__indicators {
          .van-swipe__indicator {
            background: linear-gradient(
              -10deg,
              rgba(76, 170, 241, 1),
              rgba(155, 120, 240, 1)
            );
            opacity: 0.7;
          }
          .van-swipe__indicator--active {
            background: rgb(39, 11, 104);
          }
        }
      }
    }
    .tip {
      flex-shrink: 0;
      width: 100%;
      height: 25px;
      text-align: center;
      line-height: 25px;
      background: linear-gradient(-10deg, #4caaf1, #9b78f0);
      opacity: 0.5;
      font-size: 10px;
      color: #ffffff;
    }
    .videoBaseInfo {
      flex-shrink: 0;
      flex-grow: 1;
      margin-top: 20px;
      padding: 0 15px;
      background: white;
      display: flex;
      flex-direction: column;
      .videoTitle {
        flex-shrink: 0;
        height: 120px;
        display: flex;
        .posterBox {
          width: 90px;
          height: 120px;
          border-radius: 4px 4px 4px 4px;
          img {
            width: 90px;
            height: 120px;
            border-radius: 4px 4px 4px 4px;
          }
        }
        .rightBox {
          margin-left: 15px;
          p {
            width: 200px;
            line-height: 20px;
            font-size: 12px;
            color: #aaaaaa;
            word-break: break-all;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            .episodes {
              margin-right: 10px;
            }
          }
          .videoName {
            font-size: 16px;
            font-weight: 500;
            color: #070707;
            margin-bottom: 15px;
          }
        }
      }
      .videoBrief {
        flex-shrink: 0;
        width: 100%;
        height: auto;
        margin-top: 20px;
        font-size: 12px;
        color: rgba(85, 85, 85, 1);
        .BriefBox {
          position: relative;
        }
        .shrink {
          max-height: 63px;
          overflow: hidden;
          .ellipsis {
            position: absolute;
            bottom: 2px;
            right: 0;
            line-height: 21px;
            font-size: 20px;
            padding: 0 0 0 20px;
            background: red;
            background: -webkit-linear-gradient(left, transparent, #fff 55%);
            background: -o-linear-gradient(right, transparent, #fff 55%);
            background: -moz-linear-gradient(right, transparent, #fff 55%);
            background: linear-gradient(to right, transparent, #fff 55%);
          }
        }
        .videoBriefText {
          height: auto;
          line-height: 21px;
          word-wrap: break-word;
          word-break: break-all;
        }
        .changeBrief {
          text-align: right;
          padding: 0 10px;
          font-size: 12px;
          color: rgba(75, 165, 245, 1);
          cursor: pointer;
        }
      }
      .videoDetail {
        width: 100%;
        flex-grow: 1;
        flex-shrink: 0;
        margin: 15px 0 70px 0;
        background: rgba(238, 238, 238, 1);
      }
      .videoBuy {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 60px;
        box-sizing: border-box;
        background: white;
        display: flex;
        align-items: center;
        border-top: 1px solid #4caaf1;
        div {
          flex-grow: 1;
          width: 33.3%;
          height: 60px;
          line-height: 60px;
          text-align: center;
          border-right: 1px solid #4caaf1;
        }
        .price {
          position: relative;
          .realPrice {
            font-size: 22px;
            font-weight: 500;
            color: #4caaf1;
          }
          .deletePrice {
            margin-left: 12px;
            text-decoration: line-through;
            font-size: 14px;
            color: #aaaaaa;
          }
          .rightMarker {
            position: absolute;
            top: 0;
            right: 0;
            width: 30px;
            height: 16px;
            text-align: center;
            line-height: 16px;
            border-radius: 0px 0px 0px 10px;
            background: linear-gradient(-10deg, #4caaf1, #9c78f0);
            font-size: 9px;
            color: #ffffff;
          }
        }
        .tryWatch {
          font-size: 18px;
          color: #4caaf1;
          p {
            width: 100%;
            height: 100%;
          }
        }
        .buyNow {
          background: #4caaf1;
          border: none;
          font-size: 18px;
          color: #ffffff;
        }
      }
      .shareBtn {
        position: fixed;
        top: 35px;
        right: 0;
        height: 40px;
        max-width: 100px;
        box-sizing: border-box;
        padding: 0 7px 0 18px;
        text-align: center;
        background: linear-gradient(
          -10deg,
          rgba(76, 170, 241, 1),
          rgba(155, 120, 240, 1)
        );
        border-radius: 25px 0px 0px 25px;
        font-size: 12px;
        color: rgba(255, 255, 255, 1);
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        overflow: hidden;
        p {
          max-width: 75px;
          word-break: break-all;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
</style>