<template>
  <div id="inviteLogin">
    <van-dialog
      v-model="showInviter"
      :show-confirm-button="false"
      :class="{ loginTrue: loginStatus }"
    >
      <template #title>
        <p class="avatar">
          <img :src="dialogData.avatar" />
        </p>
        <i class="closeBtn iconfont icon-guanbi3" @click="closeDialog"></i>
      </template>
      <template>
        <div class="loadBox" v-if="load">
          <van-loading
            class="load"
            type="spinner"
            color="#1989fa"
            :vertical="true"
            v-if="load"
          />
        </div>
        <div class="inviter">
          <p class="name">{{ dialogData.user_name }}</p>
          <p class="tip">
            邀您1元看大剧《{{ dialogData.program_name }}》，重温乒乓王者路
          </p>
        </div>
        <van-form
          v-if="!loginStatus"
          class="formBox"
          ref="formRef"
          @submit="login"
        >
          <!-- 手机号 -->
          <van-cell-group class="formCell telBox">
            <van-field
              placeholder="请输入手机号"
              v-model="formData.tel"
              :rules="formRule.tel"
              :error="false"
              name="tel"
              type="number"
            ></van-field>
          </van-cell-group>
          <!-- 验证码 -->
          <van-cell-group class="formCell codeBox">
            <van-field
              placeholder="请输入验证码"
              v-model="formData.code"
              :rules="formRule.code"
              :error="false"
              name="code"
              type="number"
              :maxlength="4"
            >
              <template #button>
                <van-button
                  round
                  block
                  type="info"
                  native-type="button"
                  class="getCodeBtn"
                  :class="{ notClick: !getCodeFlag }"
                  @click="getCode"
                  :disabled="!getCodeFlag"
                >
                  <span v-if="intervalFlag">({{ times }})</span>
                  获取验证码
                </van-button>
              </template>
            </van-field>
          </van-cell-group>
          <!-- 确认按钮 -->
          <van-cell-group class="formCell submitBox">
            <van-button
              round
              block
              type="info"
              native-type="submit"
              class="submitBtn"
              >接受邀请</van-button
            >
          </van-cell-group>
          <!--  -->
        </van-form>
        <div v-else class="btnBox">
          <van-button type="info" @click="closeDialog">确认</van-button>
        </div>
      </template>
    </van-dialog>
  </div>
</template>
<script>
import {
  getPhoneCode,
  codeLogin,
  createShareImg,
  getInviteLoginData,
  openidLogin,
} from "../assets/javascript/apis.js";
export default {
  name: "inviteLogin",
  props: ["showInviter", "from_user_id", "ac_id"],
  data() {
    var checkCode = (value, rule) => {
      var regCode = /^\d+$|^\d+[.]?\d+$/;
      return regCode.test(value) && value.length == 4;
    };
    return {
      loginStatus: false,
      getCodeFlag: true,
      loginFlag: true,
      intervalFlag: false,
      load: false,
      times: 60,
      dialogData: {},
      formData: {
        tel: "",
        code: "",
        codeKey: "",
      },
      formRule: {
        tel: [
          {
            required: true,
            message: "手机号不可为空",
            trigger: "onChange",
          },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "请输入正确格式的手机号",
            trigger: "onChange",
          },
        ],
        code: [
          {
            required: true,
            message: "验证码不可为空",
            trigger: "onChange",
          },
          {
            validator: checkCode,
            message: "验证码为长度为4的数字",
            trigger: "onChange",
          },
        ],
      },
    };
  },
  created() {
    this.testOpenId();
    this.getData();
  },
  methods: {
    testOpenId() {
      openidLogin({
        openid: this.$store.state.WXUserInfo.open_id,
      }).then((res) => {
        console.log(res);
        if (res.status == "none") {
          this.loginStatus = false;
        } else {
          this.loginStatus = true;
        }
      });
    },
    getData() {
      this.load = true;
      const data = {
        from_user_id: this.from_user_id,
        ac_id: this.ac_id,
      };
      console.log(data);
      getInviteLoginData(data).then((res) => {
        console.log(res);
        this.dialogData = res.data;
        this.load = false;
      });
    },
    closeDialog() {
      this.$emit("closeDialog");
    },
    //获取验证码
    getCode() {
      this.$refs.formRef
        .validate("tel")
        .then((res) => {
          if (!this.getCodeFlag) return;
          this.getCodeFlag = false;

          const data = {
            mobile: this.formData.tel,
          };
          // console.log(data);
          getPhoneCode(data).then((res) => {
            // console.log(res);
            if (res.code == 1000) {
              this.times = 60;
              this.intervalFlag = true;
              let _this = this,
                time = setInterval(function () {
                  if (_this.times <= 0) {
                    _this.getCodeFlag = true;
                    _this.intervalFlag = false;
                    clearInterval(time);
                  }
                  _this.times--;
                }, 1000);
              this.formData.codeKey = res.key;
              this.$toast({ position: "top", message: "发送验证码成功!" });
            } else {
              this.$toast({ position: "top", message: res.message });
            }
          });
        })
        .catch((res) => {
          console.log(res);
        });
    },
    //登录提交
    login(val) {
      if (this.formData.codeKey) {
        if (!this.loginFlag) return;
        this.loginFlag = false;

        const data = {
          mobile: this.formData.tel,
          verification_key: this.formData.codeKey,
          verification_code: this.formData.code,
          name: this.$store.state.WXUserInfo.name,
          avatar: this.$store.state.WXUserInfo.avatar,
          openid: this.$store.state.WXUserInfo.open_id,
        };

        codeLogin(data).then((res) => {
          console.log(res);
          this.loginFlag = true;
          if (res.status == "success") {
            this.$store.commit("setAppUserInfo", {
              token: res.token,
              id: res.data.id,
            });
            const data = {
              ac_id: this.$store.state.distributionInfo.ac_id,
              user_id: res.data.id,
            };
            createShareImg(data).then((res) => {});
            this.closeDialog();
          } else {
            this.$toast({ position: "top", message: res.message });
          }
        });
      } else {
        this.$toast({ position: "top", message: "请先获取验证码" });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
#inviteLogin {
  /deep/.van-dialog {
    width: 280px;
    height: 270px;
    background: #ffffff;
    border-radius: 5px;
    overflow: visible;
    .loadBox {
      width: 280px;
      height: 270px;
      background: #ffffff;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100000;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &.loginTrue {
      height: 180px;
    }
    .van-dialog__header {
      height: 45px;
      padding: 0;
      position: relative;
      .avatar {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        background: #fdfbfb;
        position: absolute;
        top: -38px;
        left: 50%;
        margin-left: -38px;
        border: 3px solid #fdfbfb;
        font-size: 0;
        img {
          width: 70px;
          height: 70px;
          border-radius: 50%;
          background: #fdfbfb;
        }
      }
      i {
        position: absolute;
        top: 16px;
        right: 16px;
        font-size: 28px;
        color: #aaaaaa;
      }
    }
    .van-dialog__content {
      .inviter {
        display: flex;
        flex-direction: column;
        align-items: center;
        p {
          line-height: 25px;
          text-align: center;
          font-size: 14px;
          font-weight: 500;
          color: #080808;
          word-break: break-all;
          &.name {
            width: 200px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          &.tip {
            font-size: 12px;
            color: #565656;
          }
        }
      }
      /deep/.formBox {
        margin-top: 20px;
        .formCell {
          margin-bottom: 15px;
          &:after {
            border: none;
          }
          .van-field {
            width: 100%;
            height: 30px;
            padding: 0 30px;
            overflow: inherit;
            .van-field__value {
              width: 100%;
              height: 30px;
              position: relative;
              .van-field__body {
                width: 100%;
                height: 30px;
                border-bottom: 1px solid #eee;
                input {
                  width: 100%;
                  height: 30px;
                  font-size: 14px;
                }
              }
              .van-field__error-message {
                position: absolute;
                top: 90%;
                left: 0;
                z-index: 10000;
                font-size: 12px;
              }
            }
          }
          &.codeBox {
            .getCodeBtn {
              width: 120px;
              height: 30px;
              background: linear-gradient(
                -10deg,
                rgba(76, 170, 241, 1),
                rgba(155, 120, 240, 1)
              );
              border-radius: 30px;
              font-size: 12px;
              font-weight: 500;
              color: rgba(255, 255, 255, 1);
              border: none;
            }
            .notClick {
              background: rgba(241, 243, 245, 1);
              color: rgba(7, 7, 7, 1);
            }
          }
          &.submitBox {
            width: 100%;
            display: flex;
            justify-content: center;
            margin: 25px 0 0 0;
            button {
              width: 230px;
              height: 40px;
              background: linear-gradient(-10deg, #4caaf1, #9c78f0);
              border-radius: 5px;
              font-size: 14px;
              color: #ffffff;
            }
          }
        }
      }
      .btnBox {
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 25px 0 0 0;
        button {
          width: 230px;
          height: 40px;
          background: linear-gradient(-10deg, #4caaf1, #9c78f0);
          border-radius: 5px;
          font-size: 14px;
          color: #ffffff;
        }
      }
    }
  }
}
</style>